import { motion } from "framer-motion";

import blobStyles from "./Blob.module.scss";
import { BlobPart, BlobPartCustom } from "./BlobPart";
import { AllBlobSvgValues } from "./BlobSvgValues";

// TODO: see if there is a way to only draw stroke on inside of SVG

export const BlobSvg = () => {
  const strokeWidth = 0.5;

  const draw = {
    hidden: (blobPartCustom: BlobPartCustom) => ({
      pathLength: 0,
      opacity: 0,
      fill: `${blobPartCustom.color}00`,
      strokeWidth: 0.5,
    }),
    visible: (blobPartCustom: BlobPartCustom) => {
      const extraDelay: number = 0;

      return {
        pathLength: 1,
        opacity: 1,
        fill: `${blobPartCustom.color}ff`,
        strokeWidth: 0,
        transition: {
          pathLength: {
            delay: 0.2 + extraDelay,
            type: "spring",
            duration: 10,
            bounce: 0,
          },
          opacity: { delay: 0.2 + extraDelay, duration: 0.01 },
          fill: {
            delay: 0.3 + extraDelay,
            duration: 1.5,
            ease: [1, 0, 0.8, 1],
          },
          strokeWidth: { delay: 2 + extraDelay, duration: 0.01 },
        },
      };
    },
  };

  return (
    <>
      <motion.svg
        xmlns="http://www.w3.org/2000/svg"
        className={blobStyles.blob}
        initial="hidden"
        animate="visible"
        viewBox="0 0 299.86 299.86"
      >
        <motion.g transform="translate(182.32 -18.09)">
          {AllBlobSvgValues.map((blobSvgValues, i) => (
            <BlobPart
              key={i}
              variants={draw}
              blobSvgValues={blobSvgValues}
              strokeWidth={strokeWidth}
              index={i}
            />
          ))}
        </motion.g>
      </motion.svg>
    </>
  );
};
